				"html": "<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/3/3c8584a085a0deaea66b3400e6321eeadab552a2.jpg\"></p>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/triangular_flag_on_post.png?v=3\" alt=\":triangular_flag_on_post:\" title=\":triangular_flag_on_post:\"> Remember to use <a><strong><code>Read-Search-Ask</code></strong></a> if you get stuck. Try to pair program <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/busts_in_silhouette.png?v=3\" alt=\":busts_in_silhouette:\" title=\":busts_in_silhouette:\"> and write your own code <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/pencil.png?v=3\" alt=\":pencil:\" title=\":pencil:\"></p>\n<h3>Problem Explanation:</h3>\n<p>This program is very simple, the trick is to understand what a boolean primitive is. The programs requires a true or false answer.</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Boolean\">Boolean</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" alt=\":speech_balloon:\" title=\":speech_balloon:\"> Hint: 1</h2>\n<ul>\n<li>You will need to check for the type of the parameter to see if it is a boolean.</li>\n</ul>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" alt=\":speech_balloon:\" title=\":speech_balloon:\"> Hint: 2</h2>\n<ul>\n<li>To check for the type of a parameter, you can use <code>typeof</code>.</li>\n</ul>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/speech_balloon.png?v=3\" alt=\":speech_balloon:\" title=\":speech_balloon:\"> Hint: 3</h2>\n<ul>\n<li>Since you must return true or false you can use if statements or just have it return the boolean used for the if statement.</li>\n</ul>\n<blockquote>\n<p><em>try to solve the problem now</em></p>\n</blockquote>\n<h2>Spoiler Alert!</h2>\n<p><img src=\"//discourse-user-assets.s3.amazonaws.com/original/2X/2/2d6c412a50797771301e7ceabd554cef4edcd74d.gif\" alt=\"687474703a2f2f7777772e796f75726472756d2e636f6d2f796f75726472756d2f696d616765732f323030372f31302f31302f7265645f7761726e696e675f7369676e5f322e676966.gif\"></p>\n<p><strong>Solution ahead!</strong></p>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/beginner.png?v=3\" alt=\":beginner:\" title=\":beginner:\"> Basic Code Solution:</h2>\n<pre><code>function booWho(bool) {\n  return typeof bool === 'boolean';\n}\n\n// test here\nbooWho(null);\n</code></pre>\n<p><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/rocket.png?v=3\" alt=\":rocket:\" title=\":rocket:\"> <a href=\"https://repl.it/CLnK/0\">Run Code</a></p>\n<h1>Code Explanation:</h1>\n<p>Uses the operator <code>typeof</code> to check if the variable is a boolean. If it is, it will return <code>true</code>. Otherwise, if it is any other type it will return <code>false</code>.</p>\n<h4>Relevant Links</h4>\n<ul>\n<li><a>Using typeof</a></li>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Operators/typeof\">typeof</a></li>\n</ul>\n<h2><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/clipboard.png?v=3\" alt=\":clipboard:\" title=\":clipboard:\"> NOTES FOR CONTRIBUTIONS:</h2>\n<ul>\n<li><img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" alt=\":warning:\" title=\":warning:\"> <strong>DO NOT</strong> add solutions that are similar to any existing solutions. If you think it is <strong><em>similar but better</em></strong>, then try to merge (or replace) the existing similar solution.</li>\n<li>Add an explanation of your solution.</li>\n<li>Categorize the solution in one of the following categories â€” <strong>Basic</strong>, <strong>Intermediate</strong> and <strong>Advanced</strong>. <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/traffic_light.png?v=3\" alt=\":traffic_light:\" title=\":traffic_light:\"></li>\n<li>Please add your username only if you have added any <strong>relevant main contents</strong>. (<img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/warning.png?v=3\" alt=\":warning:\" title=\":warning:\"> <strong><em>DO NOT</em></strong> <em>remove any existing usernames</em>)</li>\n</ul>\n<blockquote>\n<p>See <img src=\"https://forum.freecodecamp.com/images/emoji/emoji_one/point_right.png?v=3\" alt=\":point_right:\" title=\":point_right:\"> <a href=\"http://forum.freecodecamp.com/t/algorithm-article-template/14272\"><strong><code>Wiki Challenge Solution Template</code></strong></a> for reference.</p>\n</blockquote>",
